import React, { useState, useEffect } from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { vscDarkPlus } from 'react-syntax-highlighter/dist/esm/styles/prism';
import PythonText from '../assets/Python.json';
import JavaScriptText from '../assets/javascript.json';
import vc from '../assets/git.json';
import html from '../assets/html.json';
import css from '../assets/css.json';
import react1 from '../assets/react1.json';
import react2 from '../assets/react2.json';
import sql from '../assets/sql.json';
import django from '../assets/django.json';
import ContentPasteSharpIcon from '@mui/icons-material/ContentPasteSharp';

const languageMap = {
    python: 'python',
    javascript: 'javascript',
    versionControl: 'bash', // Assuming version control content is related to Git/bash
    html: 'html',
    css: 'css',
    react1: 'jsx',
    react2: 'jsx',
    sql: 'sql',
    django: 'python'
};

export default function Courses({ courseName }) {
    const [currentPage, setCurrentPage] = useState(1);
    const [isSidebarOpen, setIsSidebarOpen] = useState(false);
    const [copied, setCopied] = useState(false);
    const [pageName, setPageName] = useState('Topics');
    const [currentCourse, setCurrentCourse] = useState([]);

    useEffect(() => {
        // Load the appropriate course data based on courseName prop
        if (courseName === 'python') {
            setCurrentCourse(PythonText);
        } else if (courseName === 'javascript') {
            setCurrentCourse(JavaScriptText);
        } else if (courseName === 'versionControl') {
            setCurrentCourse(vc);
        } else if (courseName === 'html') {
            setCurrentCourse(html);
        } else if (courseName === 'css') {
            setCurrentCourse(css);
        } else if (courseName === 'react1') {
            setCurrentCourse(react1);
        } else if (courseName === 'react2') {
            setCurrentCourse(react2);
        } else if (courseName === 'sql') {
            setCurrentCourse(sql);
        } else if (courseName === 'django') {
            setCurrentCourse(django);
        }
        else {
            setCurrentCourse([]);
        }
        setCurrentPage(1); // Reset to first page when course changes
    }, [courseName]);

    const handlePageChange = (page, title) => {
        setCurrentPage(page);
        setPageName(title);
        setIsSidebarOpen(false);
        window.scrollTo({ top: 0, behavior: 'smooth' });
    };

    const toggleSidebar = () => {
        setIsSidebarOpen(!isSidebarOpen);
    };

    const handleCopy = (text) => {
        navigator.clipboard.writeText(text);
        setCopied(true);
        setTimeout(() => setCopied(false), 2000);
    };

    const handleNextPage = () => {
        const nextPage = currentPage < currentCourse.length ? currentPage + 1 : currentPage;
        const nextCourse = currentCourse.find(course => course.page === nextPage);
        if (nextCourse) {
            handlePageChange(nextPage, nextCourse.title);
        }
    };

    const handlePreviousPage = () => {
        const prevPage = currentPage > 1 ? currentPage - 1 : currentPage;
        const prevCourse = currentCourse.find(course => course.page === prevPage);
        if (prevCourse) {
            handlePageChange(prevPage, prevCourse.title);
        }
    };

    const course = currentCourse.find(course => course.page === currentPage);
    const syntaxLanguage = languageMap[courseName] || 'text';

    return (
        <div className="p-5 flex flex-col lg:flex-row bg-darkNavy font-[Kanit] min-h-screen">
            <div className="lg:hidden mb-4">
                <button
                    className="bg-cyellow p-2 rounded text-white"
                    onClick={toggleSidebar}
                >
                    {isSidebarOpen ? 'Close Navigation' : pageName}
                </button>
            </div>

            <div className={`sidebarzz bg-[#242e44] w-full lg:w-1/4 p-4 rounded-lg ${isSidebarOpen ? 'block' : 'hidden'} lg:block max-h-screen overflow-y-auto`}>
                <h2 className="text-xl text-white font-bold mb-4">Course Navigation</h2>
                <ul>
                    {currentCourse.map((course, index) => (
                        <li key={index} className="mb-2">
                            <button
                                className={`w-full bg-cblack text-ctext text-left p-2 rounded ${currentPage === course.page ? 'bg-cgreen' : 'bg-cblack'
                                    }`}
                                onClick={() => handlePageChange(course.page, course.title)}
                            >
                                {`${course.page}. ${course.title}`}
                            </button>
                        </li>
                    ))}
                </ul>
            </div>

            <div className="content w-full lg:w-3/4 lg:ml-6 overflow-y-auto max-h-screen">
                {course ? (
                    <div key={course.page} className="mb-8">
                        <h1 className="text-2xl text-white font-bold mb-4">{course.title}</h1>
                        {course.sections.map((section, secIndex) => (
                            <div key={secIndex} className="mb-6">
                                <h2 className="text-xl text-greyish font-semibold mb-2">{section.heading}</h2>
                                <p className="mb-4 text-ctext">{section.content}</p>
                                {section.code && (
                                    <div className="relative">
                                        <SyntaxHighlighter language={syntaxLanguage} style={vscDarkPlus}>
                                            {section.code}
                                        </SyntaxHighlighter>
                                        <button
                                            className="absolute top-2 right-2 text-white hover:cursor-pointer p-1"
                                            onClick={() => handleCopy(section.code)}
                                        >
                                            <ContentPasteSharpIcon />
                                        </button>
                                    </div>
                                )}
                                {section.img && <img className='invert' src={section.img} alt=''></img>}
                                {section.addlink && (
                                    <a
                                        className="btn btn-warning"
                                        href={section.addlink}
                                        download
                                    >
                                        Download 
                                    </a>
                                )}
                            </div>
                        ))}
                    </div>
                ) : (
                    <div className="text-white">No course content available</div>
                )}
                <div className="flex justify-between mt-8">
                    <button
                        className="p-2 w-full bg-gradient-to-r rounded-md from-green-400 to-blue-500 hover:from-pink-500 hover:to-yellow-500 flex-1 mx-2 flex items-center justify-center"
                        onClick={handlePreviousPage}
                        disabled={currentPage === 1}
                    >
                         Previous
                    </button>
                    <button
                        className="p-2 w-full bg-gradient-to-r rounded-md from-green-400 to-blue-500 hover:from-pink-500 hover:to-yellow-500 flex-1 mx-2 flex items-center justify-center"
                        onClick={handleNextPage}
                        disabled={currentPage === currentCourse.length}
                    >
                        Next 
                    </button>
                </div>

                {copied && (
                    <div className="fixed top-4 right-4 bg-green-500 text-white p-2 rounded">
                        Copied to clipboard!
                    </div>
                )}
            </div>
        </div>
    );
}
